<template>
  <Masthead />
  <div id="scene-container">
    <div class="fluid-content">
      <div class="user-center">
        <div class="userinfo-wrapper">
          <UserInfo :user-id="userId" />
        </div>
        <div class="ant-tabs">
          <el-tabs v-model="activeName" class="movie-tabs">
            <el-tab-pane v-loading="loadingFavoriteMovies" label="收藏" name="favorite">
              <div class="pane-content">
                <div
                  v-if="favoriteMovies?.length"
                  class="movies-favorite"
                  v-infinite-scroll="loadMoreFavoriteMovies"
                  infinite-scroll-disabled="disabledLoadMoreFavoriteMovies"
                >
                  <router-link
                    class="favorite-item-wrap"
                    v-for="item in favoriteMovies"
                    :key="'favorite' + item.movie?.id"
                    :to="`/movie/${item.movie?.id}`"
                  >
                    <MovieItem :movie="item.movie" />
                  </router-link>
                </div>
                <Empty
                  v-else-if="!loadingFavoriteMovies && !hasMoreFavoriteMovies"
                  type="content"
                  description="暂无内容"
                />
                <LoadingSpinner v-if="hasMoreFavoriteMovies" />
              </div>
            </el-tab-pane>
            <el-tab-pane v-if="self" v-loading="loadingMoviesHistory" label="记录" name="history">
              <div class="pane-content">
                <div
                  v-if="moviesHistory?.length"
                  class="movies-history"
                  v-infinite-scroll="loadMoreMoviesHistory"
                  infinite-scroll-disabled="disabledLoadMoreMoviesHistory"
                >
                  <template v-for="item in moviesHistory" :key="'history' + item.id">
                    <router-link v-if="item.movie?.id" class="history-item-wrap" :to="`/movie/${item.movie?.id}`">
                      <HTMovieItem :movie="item.movie" :time-ago="item.last_watch_time" :mini="false" />
                    </router-link>
                  </template>
                </div>
                <Empty
                  v-else-if="!loadingMoviesHistory && !hasMoreMoviesHistory"
                  type="content"
                  description="暂无内容"
                />
                <LoadingSpinner v-if="hasMoreMoviesHistory" />
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
  <Footer />
</template>

<script lang="ts">
import { ref, watch, toRefs } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useFavoriteMoviesQuery, useMoviesHistoryQuery } from '@/graphql';
import UserInfo from '@/views/parts/UserInfo.vue';
import MovieItem from '@/components/MovieItem.vue';
import HTMovieItem from '@/components/HTMovieItem.vue';
import LoadingSpinner from '@/components/LoadingSpinner.vue';
import Empty from '@/components/Empty.vue';

const Tabs = {
  favorite: 'favorite',
  history: 'history'
};

export default {
  components: {
    UserInfo,
    MovieItem,
    HTMovieItem,
    LoadingSpinner,
    Empty
  },
  props: ['params', 'query'],
  setup(props) {
    const { params, query } = toRefs(props);
    const userId = ref(Number(params.value?.id));
    const activeName = ref(Tabs[String(query.value?.tab)] || Tabs.favorite);
    const router = useRouter();

    const {
      loading: loadingFavoriteMovies,
      favoriteMovies,
      loadMore: loadMoreFavoriteMovies,
      hasMorePages: hasMoreFavoriteMovies,
      disabled: disabledLoadMoreFavoriteMovies
    } = useFavoriteMoviesQuery({ user_id: userId }, { enabled: !!userId.value });
    // console.log('====================================');
    // console.log('loadingFavoriteMovies', loadingFavoriteMovies);
    // console.log('favoriteMovies', favoriteMovies);
    // console.log('====================================');
    const {
      loading: loadingMoviesHistory,
      moviesHistory,
      loadMore: loadMoreMoviesHistory,
      hasMorePages: hasMoreMoviesHistory,
      disabled: disabledLoadMoreMoviesHistory
    } = useMoviesHistoryQuery({ page: 1 }, { enabled: !!userId.value });

    watch(query, (value, prevValue) => {
      activeName.value = Tabs[String(value?.tab)] || Tabs.favorite;
    });

    watch(activeName, (value, prevValue) => {
      router.replace({ query: { tab: activeName.value } });
    });

    return {
      userId,
      activeName,
      loadingFavoriteMovies,
      favoriteMovies,
      loadMoreFavoriteMovies,
      hasMoreFavoriteMovies,
      disabledLoadMoreFavoriteMovies,
      loadingMoviesHistory,
      moviesHistory,
      loadMoreMoviesHistory,
      hasMoreMoviesHistory,
      disabledLoadMoreMoviesHistory
    };
  },
  computed: {
    me() {
      return this.$store.state.user.user;
    },
    self() {
      return this.userId === this.me?.id;
    }
  }
};
</script>

