<template>
  <div class="user-info">
    <div class="info-item">
      <div class="user-avatar">
        <div
          class="avatar-wrap"
          v-loading="uploading"
          element-loading-spinner="el-icon-loading"
          element-loading-text="Loading..."
        >
          <img v-if="photo" :src="photo" class="avatar" />
          <img v-else :src="user?.avatar" class="avatar" />
          <input
            class="avatar-uploader"
            type="file"
            accept="image/png, image/jpeg, image/jpg"
            :disabled="!self || !me?.token"
            @change="onChange"
          />
        </div>
      </div>
    </div>
    <div class="info-item">
      <h2 class="user-name text-ellipsis">{{ user?.name }}</h2>
    </div>
    <div class="info-item">
      <div class="introduction">
        {{ user?.introduction || '这个人暂时没有 freestyle' }}
      </div>
    </div>
    <div class="info-item">
      <div class="interpersonal">
        <div class="meta-item" @click="showFollowModal('following')">
          <span class="meta-count">{{ user?.count_followings }}</span>
          关注
        </div>
        <div class="split">·</div>
        <div class="meta-item" @click="showFollowModal('follower')">
          <span class="meta-count">{{ user?.count_followers }}</span>
          粉丝
        </div>
      </div>
    </div>
    <div class="info-item">
      <div class="operation">
        <div v-if="self" class="button-wrap">
          <el-button type="info" round @click="handleEditorButton">
            编辑个人资料
          </el-button>
        </div>
        <div v-else-if="user" class="button-wrap">
          <FollowButton :user="user" />
        </div>
      </div>
    </div>
  </div>
  <EditUserInfoModal :user="user" />
  <FollowUsersPane :user-id="userId" />
</template>

<script lang="ts">
import { watch, computed, reactive, toRefs } from 'vue';
import { useQuery, useResult } from '@vue/apollo-composable';
import { USER_QUERY } from '@/graphql';
import store, { TYPES } from '@/store';
import EditUserInfoModal from './EditUserInfoModal.vue';
import FollowUsersPane from './FollowUsersPane.vue';

export default {
  components: {
    EditUserInfoModal,
    FollowUsersPane
  },
  props: ['userId'],
  setup(props) {
    const { userId } = toRefs(props);

    const formatUserInfo = reactive({ genderIcon: 'gender_boy', birthday_year: 2000, constellation: '天秤' });
    const { genderIcon, birthday_year, constellation } = toRefs(formatUserInfo);

    const { result, loading: userLoading } = useQuery(USER_QUERY, { id: userId });
    const user = useResult(result, null, (data: any) => data?.user);

    watch(user, (value, prevValue) => {
      const birthdayDate = new Date(value?.birthday_msg && value.birthday_msg);
      genderIcon.value = value?.gender_msg === '男' ? 'gender_boy' : 'gender_girl';
      birthday_year.value = birthdayDate.getFullYear();
    });

    return {
      userId,
      user,
      userLoading,
      genderIcon,
      birthday_year
    };
  },
  computed: {
    me() {
      return this.$store.state.user.user;
    },
    self() {
      return this.userId === this.me?.id;
    }
  },
  methods: {
    handleEditorButton() {
      this.$bus.emit('SHOW_EDIT_MODAL');
    },
    showFollowModal(tag) {
      this.$bus.emit('SHOW_FOLLOW_MODAL', { tag });
    },
    onChange(e) {
      const file = e.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = e => {
          this.photo = e.target.result;
        };
        reader.readAsDataURL(file);
        this.uploadAvatar(file);
      }
    },
    uploadAvatar(file) {
      this.uploading = true;
      var formData = new FormData();
      formData.append('avatar', file);
      var config = {
        Accept: 'application/json',
        'Content-Type': 'multipart/form-data'
      };
      this.$http
        .post(`https://juhaokantv.com/api/user/save-avatar?api_token=${this.me?.token}`, formData, config)
        .then(result => {
          this.$message.success('头像上传成功!');
          store.commit(TYPES.USER_UPDATE, { avatar: result.data });
        })
        .catch(err => {
          this.$message.error('头像上传失败!');
        })
        .finally(() => {
          this.uploading = false;
        });
    }
  },

  data() {
    return {
      photo: '',
      uploading: ''
    };
  }
};
</script>

