<template>
  <div id="edit-dialog">
    <el-dialog v-model="visible" custom-class="edit-modal" title="编辑资料" width="90%" destroy-on-close center>
      <el-form ref="editForm" :model="formData" :rules="rules">
        <el-form-item label="昵&emsp;称" prop="name">
          <el-input
            name="formData-account"
            autocomplete
            class="account-input"
            v-model="formData.name"
            :placeholder="formData.name"
            clearable
            maxlength="12"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="性&emsp;别" prop="gender">
          <el-radio v-model="formData.gender" label="男">男</el-radio>
          <el-radio v-model="formData.gender" label="女">女</el-radio>
        </el-form-item>
        <el-form-item label="生&emsp;日" prop="birthday">
          <el-date-picker
            v-model="formData.birthday"
            type="date"
            :placeholder="formData?.birthday || '请选择你的生日'"
            :default-value="new Date(formData?.birthday)"
            value-format="YYYY-MM-DD"
            class="birthday-picker"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item prop="introduction">
          <span class="introduction-label">个性签名</span>
          <el-input
            v-model="formData.introduction"
            maxlength="140"
            :placeholder="formData?.introduction"
            show-word-limit
            type="textarea"
            class="input-style"
            resize="none"
          >
          </el-input>
        </el-form-item>
        <el-button type="primary" class="submit-button" :disabled="disabled" @click="updateUserInfo">
          完成
          <i v-if="loading" class="el-icon-loading loading-icon"></i>
        </el-button>
      </el-form>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, computed, watch } from 'vue';
import { useMutation } from '@vue/apollo-composable';
import { ElMessage } from 'element-plus';
import { exceptionCapture, errorMessage } from '@/common';
import { UPDATE_USER_INFO } from '@/graphql';
import store, { Storage, TYPES } from '@/store';

export default defineComponent({
  props: ['user'],
  setup(props) {
    const { user } = toRefs(props);
    const me = ref(store.state?.user?.user);
    const visible = ref(false);
    const formData = ref({ avatar: '', name: '', gender: '男', birthday: '', introduction: '' });
    const disabled = computed(() => !formData.value.name || !formData.value.birthday || !formData.value.introduction);

    const { mutate: updateUserInfoMutation, loading } = useMutation(UPDATE_USER_INFO, () => ({
      variables: { id: user.value?.id, input: formData.value }
    }));

    async function updateUserInfo() {
      const [err, res] = await exceptionCapture(() => updateUserInfoMutation());
      if (err) {
        ElMessage.error(`修改资料出错：${errorMessage(err)}`);
      } else if (res?.data?.updateUserInfo) {
        const userInfo = Object.assign(me.value, user.value, res.data?.updateUserInfo);
        ElMessage({ message: '修改成功!', type: 'success' });
        store.commit(TYPES.USER_LOGIN, userInfo);
        Storage.setItem('user', userInfo);
        visible.value = false;
      }
    }

    watch(visible, value => {
      if (value) {
        formData.value = {
          avatar: user.value?.avatar,
          name: user.value?.name,
          gender: user.value?.gender_msg || '男',
          birthday: user.value?.birthday_msg,
          introduction: user.value?.introduction
        };
      }
    });

    return {
      visible,
      formData,
      disabled,
      loading,
      updateUserInfo
    };
  },
  mounted() {
    this.$bus.on('SHOW_EDIT_MODAL', () => {
      this.visible = true;
    });
  },
  data() {
    return {
      rules: {
        name: [
          {
            required: true,
            message: '请填写用户名',
            trigger: 'blur'
          },
          {
            max: 20,
            message: '用户名不得超过20字',
            trigger: 'blur'
          }
        ],
        gender: [
          {
            required: true,
            message: '请选择性别',
            trigger: 'blur'
          }
        ],
        birthday: [
          {
            required: true,
            message: '请选择生日',
            trigger: 'blur'
          }
        ],
        introduction: [
          {
            required: true,
            message: '请填写个性签名',
            trigger: 'blur'
          },
          {
            max: 140,
            message: '个性签名不得超过140字',
            trigger: 'blur'
          }
        ]
      }
    };
  }
});
</script>

