<template>
  <div id="follow-dialog">
    <el-dialog v-model="visible" custom-class="follow-modal" width="90%" destroy-on-close center>
      <el-tabs v-model="activeName" class="user-tabs">
        <el-tab-pane label="关注" name="following">
          <div class="pane-content">
            <div class="user-item-wrap" v-for="item in followedUsers" :key="'followed' + item.people?.id">
              <UserItem v-if="item.people?.id" :user="item.people" />
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="粉丝" name="follower">
          <div class="pane-content">
            <div class="user-item-wrap" v-for="item in followers" :key="'followed' + item.user?.id">
              <UserItem v-if="item?.id" :user="item.user" />
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs } from 'vue';
import { useFollowedUsersQuery, useFollowersQuery } from '@/graphql';
import UserItem from '@/components/UserItem.vue';

export default defineComponent({
  components: { UserItem },
  props: ['userId'],
  setup(props) {
    const { userId } = toRefs(props);
    const visible = ref(false);
    const activeName = ref('following');

    // 关注的用户
    const {
      followedUsers,
      loading: followedLoading,
      error: followedError,
      refetch: followedRefetch,
      loadMore: loadMoreFollowed,
      isFinished: followedFinished,
      loadingMore: followedLoadingMore
    } = useFollowedUsersQuery({ user_id: userId.value }, { fetchPolicy: 'network-only', enabled: !!userId.value });

    // 我的粉丝
    const {
      followers,
      loading: followersLoading,
      error: followersError,
      refetch: followersRefetch,
      loadMore: loadMoreFollowers,
      isFinished: followersFinished,
      loadingMore: followersLoadingMore
    } = useFollowersQuery({ user_id: userId.value }, { fetchPolicy: 'network-only', enabled: !!userId.value });

    return {
      visible,
      activeName,
      followedUsers,
      followedLoading,
      followedError,
      followedRefetch,
      loadMoreFollowed,
      followedFinished,
      followedLoadingMore,
      followers,
      followersLoading,
      followersError,
      followersRefetch,
      loadMoreFollowers,
      followersFinished,
      followersLoadingMore
    };
  },
  mounted() {
    this.$bus.on('SHOW_FOLLOW_MODAL', ({ tag }) => {
      this.activeName = tag === 'follower' ? 'follower' : 'following';
      this.followedRefetch();
      this.followersRefetch();
      this.visible = true;
    });
  }
});
</script>

